import React, { Component } from "react";

class Card extends Component {
  state = {
    Result: "Result",
  };
  handleResult = () => {
    this.props.match.matchStarted
      ? this.setState({ Result: this.props.match.winner_team })
      : this.setState({ Result: "Not Start Yet" });
  };

  render() {
    return (
      <>
        <div className="card">
          <div className="date">
            <div className="date-d"> DATE - {Date(this.props.match.date)}</div>
          </div>

          <div className="teams">
            <div className="first-team">
              <div className="team-text">{this.props.match["team-1"]}</div>
            </div>

            <div className="vs">V/S</div>

            <div className="second-team">
              <div className="team-text">{this.props.match["team-2"]}</div>
            </div>
          </div>

          <button
            className="result-button btn btn-primary"
            onClick={this.handleResult}
          >
            {this.state.Result}
          </button>
        </div>
      </>
    );
  }
}

export default Card;
